@import url("https://fonts.googleapis.com/css2?family=Inter:wght@300;400;500;600;700&display=swap");

:root {
  --primary-color: #3498db;
  --secondary-color: #e74c3c;
  --background-color: #1a1a1a;
  --card-background: #2c2c2c;
  --text-color: #f0f0f0;
  --text-muted: #a0a0a0;
  --navbar-color: #252525;
  --shadow-color: rgba(0, 0, 0, 0.3);
  --success-color: #2ecc71;
  --warning-color: #f39c12;
}

body {
  font-family: "Inter", sans-serif;
  background-color: var(--background-color);
  color: var(--text-color);
  margin: 0;
  padding: 0;
  line-height: 1.6;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
}

.app {
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
  flex-grow: 1;
}

.navbar {
  background-color: var(--navbar-color);
  padding: 15px 0;
  box-shadow: 0 2px 10px var(--shadow-color);
  position: sticky;
  top: 0;
  z-index: 1000;
}

.navbar ul {
  list-style-type: none;
  padding: 0;
  margin: 0;
  display: flex;
  justify-content: center;
}

.navbar ul li {
  margin: 0 15px;
}

.navbar ul li a {
  color: var(--text-color);
  text-decoration: none;
  font-weight: 500;
  font-size: 1.1em;
  transition: all 0.3s ease;
  position: relative;
}

.navbar ul li a::after {
  content: "";
  position: absolute;
  width: 100%;
  height: 2px;
  bottom: -5px;
  left: 0;
  background-color: var(--primary-color);
  transform: scaleX(0);
  transition: transform 0.3s ease;
}

.navbar ul li a:hover::after {
  transform: scaleX(1);
}

button {
  background-color: var(--primary-color);
  color: var(--text-color);
  padding: 12px 24px;
  cursor: pointer;
  border: none;
  border-radius: 25px;
  font-size: 1em;
  font-weight: 600;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px var(--shadow-color);
}

button:hover {
  background-color: #2980b9;
  transform: translateY(-2px);
  box-shadow: 0 6px 8px var(--shadow-color);
}

button:active {
  transform: translateY(0);
}

.home-page,
.contacts-page,
.received-alerts-page,
.alert-page {
  background-color: var(--card-background);
  border-radius: 15px;
  padding: 30px;
  margin-top: 30px;
  box-shadow: 0 10px 20px var(--shadow-color);
  transition: all 0.3s ease;
}

.home-page:hover,
.contacts-page:hover,
.received-alerts-page:hover,
.alert-page:hover {
  transform: translateY(-5px);
  box-shadow: 0 15px 30px var(--shadow-color);
}

h1,
h2,
h3 {
  color: var(--primary-color);
  margin-bottom: 20px;
  font-weight: 600;
}

h1 {
  font-size: 2.5em;
  text-align: center;
  margin-bottom: 30px;
  background: linear-gradient(
    120deg,
    var(--primary-color),
    var(--secondary-color)
  );
  -webkit-background-clip: text;
  -webkit-text-fill-color: transparent;
  background-clip: text;
}

input[type="text"] {
  width: 100%;
  padding: 12px;
  margin-bottom: 15px;
  border: 2px solid #444;
  border-radius: 25px;
  font-size: 1em;
  transition: all 0.3s ease;
  background-color: #333;
  color: var(--text-color);
}

input[type="text"]:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(52, 152, 219, 0.2);
  outline: none;
}

.alert-card {
  background-color: #3c3c3c;
  border-left: 5px solid var(--secondary-color);
  padding: 20px;
  margin-bottom: 20px;
  border-radius: 10px;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px var(--shadow-color);
}

.alert-card:hover {
  transform: translateY(-5px) rotate(1deg);
  box-shadow: 0 8px 12px var(--shadow-color);
}

ul {
  padding-left: 20px;
}

li {
  margin-bottom: 15px;
  position: relative;
}

li::before {
  content: "â€¢";
  color: var(--primary-color);
  font-weight: bold;
  display: inline-block;
  width: 1em;
  margin-left: -1em;
}

/* Emergency Button */
.emergency-button {
  background-color: var(--secondary-color);
  color: white;
  padding: 15px 30px;
  font-size: 1.2em;
  border-radius: 50px;
  border: none;
  cursor: pointer;
  transition: all 0.3s ease;
  box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  display: block;
  margin: 20px auto;
}

.emergency-button:hover {
  background-color: #c0392b;
  transform: scale(1.05);
  box-shadow: 0 6px 8px rgba(0, 0, 0, 0.15);
}

.emergency-button:active {
  transform: scale(0.95);
}

/* Responsive design */
@media (max-width: 768px) {
  .navbar ul {
    flex-direction: column;
    align-items: center;
  }

  .navbar ul li {
    margin: 10px 0;
  }

  .app {
    padding: 10px;
  }

  h1 {
    font-size: 2em;
  }

  .emergency-button {
    font-size: 1em;
    padding: 12px 24px;
  }
}

/* Animations */
@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

@keyframes pulse {
  0% {
    transform: scale(1);
  }
  50% {
    transform: scale(1.05);
  }
  100% {
    transform: scale(1);
  }
}

.home-page,
.contacts-page,
.received-alerts-page,
.alert-page {
  animation: fadeIn 0.5s ease-out;
}

.emergency-button:hover {
  animation: pulse 1s infinite;
}

/* Additional modern touches */
::selection {
  background-color: var(--primary-color);
  color: var(--text-color);
}

::-webkit-scrollbar {
  width: 10px;
}

::-webkit-scrollbar-track {
  background: var(--background-color);
}

::-webkit-scrollbar-thumb {
  background: var(--primary-color);
  border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
  background: #2980b9;
}

/* Ticket Booking Form */
.booking-form {
  display: grid;
  gap: 20px;
  margin-top: 20px;
}

.booking-form label {
  font-weight: 500;
  color: var(--text-muted);
}

.booking-form input[type="date"],
.booking-form select {
  width: 100%;
  padding: 12px;
  border: 2px solid #444;
  border-radius: 25px;
  font-size: 1em;
  transition: all 0.3s ease;
  background-color: #333;
  color: var(--text-color);
}

.booking-form input[type="date"]:focus,
.booking-form select:focus {
  border-color: var(--primary-color);
  box-shadow: 0 0 0 2px rgba(52, 152, 219, 0.2);
  outline: none;
}

/* Success Message */
.success-message {
  background-color: var(--success-color);
  color: white;
  padding: 15px;
  border-radius: 10px;
  margin-top: 20px;
  text-align: center;
  animation: fadeIn 0.5s ease-out;
}

/* Warning Message */
.warning-message {
  background-color: var(--warning-color);
  color: white;
  padding: 15px;
  border-radius: 10px;
  margin-top: 20px;
  text-align: center;
  animation: fadeIn 0.5s ease-out;
}
